/* eslint-disable */
import react from 'react'
import { View } from '@tarojs/components'
import { Table, ITableProps } from '@antmjs/vantui'

const columns: ITableProps['columns'] = [
  {
    title: '',
    dataIndex: 'name',
    fixed: 'left',
    render: (val) => <View style={{ color: '#2196F3' }}>{val}</View>,
  },
  {
    title: '',
    dataIndex: 'age',
    align: 'center',
    sort: true,
  },
  {
    title: '',
    dataIndex: 'account',
    sort: true,
  },
  {
    title: '',
    dataIndex: 'address',
  },
  {
    title: '',
    dataIndex: 'hobby',
  },
]
export default function Demo() {
  const [state, changeState] = react.useState({
    dataSource: [],
    loading: false,
  })

  const setState = (stateNew) => {
    changeState({
      ...state,
      ...stateNew,
    })
  }

  react.useEffect(() => {
    setState({ loading: true })
    setTimeout(() => {
      setState({
        loading: false,
        dataSource: [
          {
            key: '1',
            name: '',
            age: 37,
            account: 20000,
            address: '1',
            hobby: '',
          },
          {
            key: '2',
            name: '',
            age: 32,
            account: 50000,
            address: '',
            hobby: '',
          },
          {
            key: '36',
            name: '',
            age: 31,
            account: 40000,
            address: '',
            width: 200,
            hobby: '',
          },
        ],
      })
    }, 2000)
  }, [])

  const sortAction = (col) => {
    const { sortOrder, dataIndex } = col
    const { dataSource } = state
    if (sortOrder !== 'descend') {
      dataSource.sort(function (a, b) {
        return a[dataIndex] - b[dataIndex]
      })
    } else {
      dataSource.sort(function (a, b) {
        return b[dataIndex] - a[dataIndex]
      })
    }

    setState({
      dataSource,
    })
  }
  return (
    <Table
      columns={columns}
      dataSource={state.dataSource}
      sortChange={sortAction}
      loading={state.loading}
      scroll={{ x: 500 }}
      rowKey="title"
    />
  )
}
